import { selector, selectorFamily } from 'recoil';
import { TodoDataState } from 'recoil/atoms/todoDataState';
import { TodoItem } from 'types/TodoList';

export const todoDataAllSelector = selector({
  key: 'todoDataAllSelector',
  get: ({ get }) => get(TodoDataState),
  set: ({ set }, newValue) => set(TodoDataState, newValue),
});

export const todoDataOneSelector = selectorFamily<TodoItem, number>({
  key: 'todoDataOneSelector',
  get:
    (idx: number) =>
    ({ get }) => {
      const data = get(TodoDataState);
      return data[idx];
      // console.log('todoDataOneState', dataOne);
      // return dataOne[idx] ? dataOne[idx] : [];
    },

  // optional set
  set:
    (idx: number) =>
    ({ get, set }, newOne) => {
      // console.log('todoDataOneState', 'Set', TodoDataState);
      let data = get(TodoDataState); // recoil  
      data = [...data]; //      , 
      data[idx] = newOne as TodoItem; //     
      set(TodoDataState, data); //   
    },
});
