/**
 *    
 * @param email
 * @returns boolean
 */
export function validateEmail(email: string): boolean {
  const emailRegex = /^[\w\-\.]+@([\w-]+\.)+[\w-]{2,4}$/;
  return emailRegex.test(email);
}

/**
 *     : , ,   8 
 * @param password
 * @returns boolean
 */
export function validatePassword(password: string): boolean {
  const passwordRegex =
    /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{8,}$/;
  return passwordRegex.test(password);
}
